export default class Marquee {
    marqueeBoxId = 'marqueeBox';
    marqueeBox = null;

    marqueeId = 'marquee';
    marquee = null;

    constructor() {
        this.fillMarquee();
    }

    // eslint-disable-next-line
    fillMarquee() {
        this.marqueeBox = document.getElementById(this.marqueeBoxId);
        this.marquee = this.marqueeBox.querySelector(`.${this.marqueeId}`);

        const countRepeat = Math.round((window.innerWidth * 2) / this.marquee.offsetWidth);

        for (let i = 0; i < countRepeat; i += 1) {
            const repeatMarquee = document.createElement(this.marquee.tagName);

            repeatMarquee.innerHTML = this.marquee.innerHTML;
            Array.from(this.marquee.classList).forEach((elClass) => {
                repeatMarquee.classList.add(elClass);
            });

            this.marqueeBox.appendChild(repeatMarquee);
        }
    }
}
