import Swiper, { Pagination } from 'swiper';
import isMobilePt from './utils/functions/isMobilePt';

export default class Swipers {
    constructor() {
        if (isMobilePt()) {
            this.initSwipers();
        } else {
            this.makeLinkCard();
        }
    }

    // eslint-disable-next-line
    initSwipers() {
        Array.from(document.querySelectorAll('.swiper-goods')).forEach((swiperEl) => {
            // eslint-disable-next-line
            const swiperInstance = new Swiper(swiperEl, {
                modules: [Pagination],
                pagination: {
                    el: '.section-goods__pagination'
                }
            });
        });
    }

    // eslint-disable-next-line
    makeLinkCard() {
        Array.from(document.querySelectorAll('.section-goods')).forEach((sectionGoods) => {
            Array.from(sectionGoods.querySelectorAll('.section-goods__card')).forEach((card) => {
                console.log(card);
            });
        });
    }
}
